function compare(a, b) {
  // Use toUpperCase() to ignore character casing
  const fechaA = a.cant;
  const fechaB = b.cant;

  let comparison = 0;
  if (fechaA > fechaB) {
    comparison = -1;
  } else if (fechaA < fechaB) {
    comparison = 1;
  }
  return comparison;
}

function dateFormat(num) {
  if (num) {
    const fechaxl = new Date((num - (25567 + 1)) * 86400 * 1000);
    let xldd = fechaxl.getDate();
    let xlmm = fechaxl.getMonth() + 1;
    let xlyyyy = fechaxl.getFullYear();
    if (xldd < 10) xldd = "0" + xldd;
    if (xlmm < 10) xlmm = "0" + xlmm;
    let xldate = xlyyyy + "-" + xlmm + "-" + xldd;
    return xldate;
  }
}

class Tabla {
  constructor(id, name, price, image, cat, cant) {
    this.id = id;
    this.name = name;
    this.price = price;
    this.image = image;
    this.cat = cat;
    this.cant = cant;
  }
}

class ItemCarrito {
  constructor(id, name, price, cant, amt) {
    this.id = id;
    this.name = name;
    this.price = price;
    this.cant = cant;
    this.amt = amt;
  }
}

function currencyFormat(num) {
  return "$" + num.toFixed(2).replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1,");
}

